.planScreen__plan{
  display: flex;
  justify-content: space-between;
  padding: 20px;
  opacity: 0.8 /* For the hover effect */
}

.planScreen__plan:hover{
  opacity: 1;
}

.planScreen__plan > button{
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  background-color: #e50914;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.planScreen__plan--disabled > button{
  background-color: gray  !important
}

.planScreen__info > h5{
  font-size: 16px;
  font-weight: 600;
}

.planScreen__info > h6{
  font-size: 14px;
  font-weight: 600;
}