.profileScreen{
  height: 100vh;
  color: white;
}

.profileScreen__body{
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 8%;
  max-width: 800px;
}

.profileScreen__info{
  display: flex;
}



.profileScreen__info > img {
  height: 100px;
}

.profileScreen__body > h1{
  font-size: 40px;
  font-weight: 400;
  border-bottom: 1px solid #282c2d;
  margin-bottom: 20px;
}

.profileScreen__details{
  color: white;
  margin-left: 25px;
  flex: 1;
}

.profileScreen__details > h2{
  background-color: grey;
  padding: 15px;
  font-size: 15px;
  padding-left: 20px;
}

.profileScreen__signOut {
  padding: 10px 20px;
  font-size: 1rem;
  margin-top: 5%;
  width: 100%;
  color: #fff;
  background-color: #e50914;
  border: none;
  cursor: pointer;
}

.profileScreen__plans{
  margin-top: 20px;
}

.profileScreen__plans > h3{
  border-bottom: 1px solid #282c2d;
  padding-bottom: 10px;
}

@media screen and (max-width: 890px) {
  .profileScreen__body{
    padding-top: 150px;
    width: 70%;
    min-width: 600px;
  }
}